import { useState } from 'react'

const InfoTab = ({ title, icon, content }) => {
  const [open, setOpen] = useState(false)
  return (
    <div className={`v-city-info-tab ${open ? 'open' : ''}`}>
      <div className="v-city-info-tab__header" onClick={() => setOpen(!open)}>
        <strong>{title}</strong>
        <img src={`images/icons/${icon}`} alt={title} />
      </div>
      <div
        className="v-city-info-tab__content"
        dangerouslySetInnerHTML={{ __html: content }}
      ></div>
    </div>
  )
}

export default function InfoTabs({ data }) {
  return (
    <div className="v-city-info">
      {data.map((props, i) => (
        <InfoTab {...props} key={i} />
      ))}
    </div>
  )
}
