import { useState, useEffect } from 'react'
import data from 'mock/data'

const GroupVideo = ({ video, image, title }) => {
  return (
    <video
      className="v-group-header-video"
      src={`video/${video}`}
      playsInline
      autoPlay
      muted
      loop
    >
      <GroupImage image={image} title={title} />
    </video>
  )
}
const GroupImage = ({ image, title }) => {
  return (
    <img
      src={`images/mobile/${image}`}
      className="v-group-header-image"
      alt={title}
    />
  )
}

const Group = ({ id, title, image, video, items, onClick }) => {
  return (
    <div className="v-group" id={id}>
      <div className="v-group-header">
        {video ? (
          <GroupVideo image={image} title={title} video={video} />
        ) : (
          <GroupImage image={image} title={title} />
        )}
        <div className="v-group-header-title">{title}</div>
      </div>
      <div className="v-group-info-wrapper">
        {items.map((item, i) => (
          <div
            className={`v-group-info ${item.marker}`}
            onClick={() => onClick(item.title)}
            key={i}
          >
            <div className="v-group-info-marker">
              <img src={`images/markers/${item.marker}.svg`} alt={item.title} />
            </div>
            <div className="v-group-info-title">
              {item.titleMobile ?? item.title}
            </div>
          </div>
        ))}
      </div>
    </div>
  )
}

const Modal = ({ data, closeModal }) => {
  useEffect(() => {
    const handleOutsideClick = (e) => {
      e.target.classList.contains('v-modal-wrapper') && closeModal()
    }
    document.addEventListener('click', handleOutsideClick, false)

    return () => {
      document.removeEventListener('click', handleOutsideClick, false)
    }
  }, [closeModal])

  return (
    <div className="v-modal-wrapper">
      <div className="v-modal">
        <div className="v-modal-close" onClick={closeModal}>
          &times;
        </div>
        <div className="v-modal-header">
          <div className="v-modal-header-marker">
            <img src={`images/markers/${data.marker}.svg`} alt={data.title} />
          </div>
          <div className="v-modal-header-title">{data.title}</div>
        </div>
        <div
          className="v-modal-body"
          dangerouslySetInnerHTML={{ __html: data.description }}
        ></div>
      </div>
    </div>
  )
}

export default function AppMobile() {
  const [showModal, setShowModal] = useState(false)
  const [modalContent, setModalContent] = useState(null)
  const showInfoModal = (clickedTitle) => {
    const { title, marker, description } = data.filter(
      (item) => item.title === clickedTitle
    )[0]
    setModalContent({ title, marker, description })
    setShowModal(true)
  }

  const groups = [
    {
      id: 'loyalty',
      title: 'LOYALTY',
      image: 'loyalty.jpg',
      video: 'loyalty.mp4'
    },
    {
      id: 'shop',
      title: 'SHOPinSHOP',
      image: 'shop.jpg',
      video: null
    },
    {
      id: 'entertainment',
      title: 'ENTERTAINMENT',
      image: 'entertainment.jpg',
      video: null
    }
  ]
  groups.forEach(({ id }, i) => {
    groups[i].items = [...data.filter(({ group }) => group === id)]
    groups[i].onClick = showInfoModal
  })

  return (
    <>
      {showModal && (
        <Modal data={modalContent} closeModal={() => setShowModal(false)} />
      )}
      {groups.map((props) => (
        <Group {...props} key={props.id} />
      ))}
    </>
  )
}
